<template>
  <div class="manage">
    <el-dialog
      title="新增用户"
      :visible.sync="dialogVisible"
      width="40%"
      :beforeClose="handleClose"
    >
      <el-form
        ref="form"
        :model="form"
        label-width="80px"
        :inline="true"
        :rules="rules"
      >
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
        </el-form-item>
        <el-form-item label=" 年龄" prop="age">
          <el-input v-model="form.age" placeholder="请输入年龄"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model="form.sex" placeholder="请选择性别">
            <el-option label="男" :value="1"></el-option>
            <el-option label="女" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出生日期" prop="birth">
          <el-col :span="15">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="form.birth"
              value-format="yyyy-MM-dd"
              style="width: 100%"
            ></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label=" 地址" prop="addr">
          <el-input v-model="form.addr" placeholder="请输入地址"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="submitForm">确 定</el-button>
      </span>
    </el-dialog>
    <div class="manage-header">
      <el-button @click="handleAdd" type="primary"> + 新增 </el-button>
      <!-- form搜索区域 -->
      <el-form :model="userForm" :inline="true">
        <el-form-item>
          <el-input  placeholder="请输入用户名称" v-model="userForm.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>

      </el-form>
    </div>
    <div class="common-table">
      <el-table :data="tableData" style="width: 100%" height="600px" stripe>
        <el-table-column prop="name" label="姓名"> </el-table-column>
        <el-table-column prop="age" label="年龄"> </el-table-column>
        <el-table-column prop="sex" label="性别">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{
              scope.row.sex === 1 ? "男" : "女"
            }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="birth" label="出生日期"> </el-table-column>
        <el-table-column prop="addr" label="地址"> </el-table-column>
        <el-table-column prop="" label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >删除</el-button
            >
          </template> </el-table-column
        >` `
      </el-table>
      <!-- 分页 -->
      <div class="pager">
        <el-pagination
          layout="prev, pager, next"
          :total="total"
          @current-change="handlePageChange"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getUser, addUser, editUser, deleteUser } from "../api";
export default {
  name: "MyUser",

  data() {
    return {
      dialogVisible: false,
      form: {
        name: "",
        age: "",
        sex: "",
        birth: "",
        addr: "",
      },
      rules: {
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        age: [{ required: true, message: "请输入年龄", trigger: "blur" }],
        sex: [{ required: true, message: "请选择性别", trigger: "blur" }],
        birth: [{ required: true, message: "请选择出生日期", trigger: "blur" }],
        addr: [{ required: true, message: "请输入地址", trigger: "blur" }],
      },
      tableData: [
        // { birth: "2021-01-01", name: "张三", addr: "北京市海淀区" },
      ],
      modelType: 0, //0新增，1编辑
      total: 0, //当前table数据总条数
      pageData: {
        page: 1,
        limit: 10,
      },
      userForm: {
        name: "",
      },
    };
  },

  mounted() {
    //页面首次加载调用获取用户列表数据
    this.getList();
  },

  methods: {
    //提交用户表单
    submitForm() {
      this.$refs.form.validate((valid) => {
        //valid为true时才能继续对表单数据进行处理
        //后续对表单数据的处理
        if (valid) {
          if (this.modelType === 0) {
            //👉新增用户
            addUser(this.form).then(() => {
              //新增成功后重新获取用户列表数据
              this.getList();
            });
          } else {
            //👉编辑用户
            editUser(this.form).then(() => {
              //编辑成功后重新获取用户列表数据
              this.getList();
            });
          }
          //提交表单数据
          // console.log(this.form, "form");
          //关闭弹窗之前先重置表单
          this.$refs.form.resetFields();
          //关闭弹窗
          this.dialogVisible = false;
        }
      });
    },
    handleClose() {
      //关闭弹窗时重置表单
      this.$refs.form.resetFields();
      //关闭弹窗
      this.dialogVisible = false;
    },
    getList() {
      //获取列表的数据
      getUser({ params: { ...this.userForm, ...this.pageData } }).then(
        (data) => {
          // console.log(data.data.count);
          this.tableData = data.data.list;
          this.total = data.data.count || 0;
        }
      );
    },
    handleAdd() {
      this.modelType = 0;
      //点击新增按钮时，打开新增弹窗
      this.dialogVisible = true;
    },
    handleEdit(row) {
      //👉编辑用户
      this.modelType = 1;
      this.dialogVisible = true;
      //👉👉注意需要对当前行数据进行深拷贝，否则会出错
      this.form = JSON.parse(JSON.stringify(row));
      //关闭弹窗之前先重置表单
      this.$refs.form.resetFields();
    },
    handleDelete(row) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          deleteUser({ id: row.id }).then(() => {
            //删除成功后重新获取用户列表数据
            this.getList();
          });
          this.$message({
            type: "success",
            message: "删除成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    //选择页码的回调函数
    handlePageChange(val) {
      console.log(val, "页码");
      this.pageData.page = val;
      this.getList(); //重新获取用户列表数据
    },
    //搜索用户的回调函数
    onSubmit(){
      this.getList(); //重新获取用户列表数据
    }
  },
};
</script>
<style lang="less" scoped>
.manage {
  height: 90%;
  .manage-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .common-table {
    position: relative;
    height: calc(100% - 62px);
    .pager {
      position: absolute;
      bottom: 0;
      right: 20px;
    }
  }
}
</style>
